<!-- MyFooter.vue -->
<template>
  <div class="candidate-footer" v-show="total">
    <label>
      <input
        type="text"
        placeholder="輸入您的名稱"
        v-model="person"
        :disabled="isAdmin" 
      />
    </label>
    <button
      class="btn btn-primary"
      @click="vote"
      :disabled="isAdmin" 
    >
      投票
    </button>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'MyFooter',
  props: ['candidates', 'checkAllCandidate', 'clearAllCandidate', 'isAdmin'],
  data() {
    return {
      person: '',
    };
  },
  computed: {
    total() {
      return this.candidates.length;
    },
    selectedTotal() {
      return this.candidates.reduce(
        (pre, candidate) => pre + (candidate.selected ? 1 : 0),
        0
      );
    },
    isAll: {
      get() {
        return this.selectedTotal === this.total && this.total > 0;
      },
      set(value) {
        this.checkAllCandidate(value);
      },
    },
  },
  methods: {
    clearAll() {
      this.clearAllCandidate();
    },
    vote() {
      if (this.selectedTotal === 0) {
        alert('請選擇要投票的候選項目');
        return;
      }
      
      if (!this.person.trim()) { // 判斷名稱是否為空
        alert('您的名稱不能為空');
        return;
      }

      const selectedCandidates = this.candidates.filter(
        candidate => candidate.selected
      );
      const voteUrl = `/candidate/update?person=${encodeURIComponent(
        this.person
      )}`;

      axios
        .post(voteUrl, selectedCandidates)
        .then(() => {
          this.candidates.forEach(candidate => {
            if (candidate.selected) {
              candidate.count++;
              candidate.selected = false;
            }
          });
          this.person = '';
        })
        .catch(error => {
          console.error('投票錯誤：', error);
        });
    },
  },
};
</script>

<style scoped>
.candidate-footer {
  height: 40px;
  line-height: 40px;
  margin-top: 5px;
}

.candidate-footer label {
  display: inline-block;
  margin-right: 20px;
  cursor: pointer;
}

.candidate-footer label {
  position: relative;
  top: -1px;
  vertical-align: middle;
  margin-right: 5px;
}

.candidate-footer button {
  float: right;
  margin-top: 5px;
}

.candidate-footer input {
  width: 490px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.candidate-footer input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
}
</style>
