<!-- MyHeader.vue -->
<template>
  <div class="candidate-header">
    <input
      type="text"
      placeholder="請輸入要新增的投票項目，並按下Enter確認"
      v-model="name"
      @keyup.enter="add"
      :disabled="mode === '使用者模式'"
    />
    <button class="btn btn-primary" @click="toggleMode">{{ mode }}</button>
  </div>
</template>

<script>
import { nanoid } from 'nanoid';
import axios from 'axios';

export default {
  name: 'MyHeader',
  props: ['addCandidate'],
  data() {
    return {
      name: '',
      mode: '使用者模式', // 新增模式狀態
    };
  },
  methods: {
    async add() {
      if (!this.name.trim()) return alert('輸入不能為空');
      const candidateObj = {
        id: nanoid(),
        name: this.name,
        selected: false,
        count: 0,
      };

      try {
        await axios.post('/candidate/create', candidateObj);
        this.addCandidate(candidateObj);
        this.name = '';
      } catch (error) {
        console.error('添加投票項目失敗', error);
      }
    },
    toggleMode() {
      this.mode = this.mode === '使用者模式' ? '管理員模式' : '使用者模式'; // 切換模式
      this.$emit('toggleMode', this.mode); // 傳遞模式狀態給父組件
    },
  },
};
</script>

<style scoped>
.candidate-header input {
  width: 450px;
  height: 28px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 4px 7px;
}

.candidate-header input:focus {
  outline: none;
  border-color: rgba(82, 168, 236, 0.8);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
}

.candidate-header button {
  float: right;
  margin-top: 3px;
}

</style>