<!-- MyList.vue -->
<template>
  <ul class="candidate-main">
    <MyItem
      v-for="candidateObj in localCandidates"
      :key="candidateObj.id"
      :candidate="candidateObj"
      :checkCandidate="checkCandidate"
      :deleteCandidate="deleteCandidate"
      :isAdmin="isAdmin"
      @delete="handleDelete"
    />
  </ul>
</template>

<script>
import MyItem from './MyItem';

export default {
  name: 'MyList',
  components: { MyItem },
  props: ['candidates', 'checkCandidate', 'deleteCandidate', 'isAdmin'], // 新增 isAdmin prop
  data() {
    return {
      localCandidates: [...this.candidates],
    };
  },
  watch: {
    candidates(newCandidates) {
      this.localCandidates = [...newCandidates];
    },
  },
  methods: {
    handleDelete(id) {
      if (this.isAdmin) { // 只有在管理員模式下才能刪除
        this.localCandidates = this.localCandidates.filter(candidate => candidate.id !== id);
      }
    },
  },
};
</script>

<style scoped>
.candidate-main {
  margin-left: 0px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 0px;
}

.candidate-empty {
  height: 40px;
  line-height: 40px;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding-left: 5px;
  margin-top: 10px;
}
</style>
