<!-- MyItem.vue -->
<template>
  <li>
    <label>
      <input
        type="checkbox"
        :checked="candidate.selected"
        @change="handleCheck(candidate.id)"
        :disabled="isAdmin"
      />
      <span>{{ candidate.name }}</span>
      <span class="count">票數：{{ candidate.count }}</span>
    </label>
    <button
      class="btn btn-danger"
      @click="handleDelete(candidate.id)"
      v-show="isAdmin"
    >
      刪除
    </button>
  </li>
</template>

<script>
import axios from 'axios';
export default {
  name: 'MyItem',
  props: ['candidate', 'checkCandidate', 'deleteCandidate', 'voteForCandidate', 'isAdmin'], // 新增 isAdmin prop
  methods: {
    handleCheck(id) {
      this.checkCandidate(id);
    },
    handleDelete(id) {
      if (this.isAdmin && confirm('確定刪除嗎？')) {
        axios
          .delete(`/candidate/${id}`)
          .then(() => {
            this.$emit('delete', id);
          })
          .catch(error => {
            console.error('刪除候選項目錯誤：', error);
          });
      }
    },
    handleVote(id) {
      this.voteForCandidate(id);
    },
  },
};
</script>

<style scoped>
li {
  list-style: none;
  height: 36px;
  line-height: 36px;
  padding: 0 5px;
  border-bottom: 1px solid #ddd;
}

li label {
  float: left;
  cursor: pointer;
}

li label li input {
  vertical-align: middle;
  margin-right: 6px;
  position: relative;
  top: -1px;
}

li .count {
  margin-left: 10px;
  font-size: 12px;
  color: #888;
}

li button {
  float: right;
  display: none;
  margin-top: 3px;
}

li:before {
  content: initial;
}

li:last-child {
  border-bottom: none;
}

li:hover {
  background-color: #ddd;
}

li:hover button {
  display: block;
}
</style>
