<!-- App.vue -->
<template>
  <div id="root">
    <div class="candidate-container">
      <div class="candidate-wrap">
        <MyHeader :addCandidate="addCandidate" @toggleMode="toggleMode" />
        <MyList
          :candidates="candidates"
          :checkCandidate="checkCandidate"
          :deleteCandidate="deleteCandidate"
          :isAdmin="isAdmin"
        />
        <MyFooter
          :candidates="candidates"
          :checkAllCandidate="checkAllCandidate"
          :clearAllCandidate="clearAllCandidate"
          :isAdmin="isAdmin"
        />
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import MyHeader from './components/MyHeader';
import MyList from './components/MyList';
import MyFooter from './components/MyFooter.vue';

export default {
  name: 'App',
  components: { MyHeader, MyList, MyFooter },
  data() {
    return {
      candidates: [],
      isAdmin: false, // 新增 isAdmin 狀態
    };
  },
  created() {
    axios.get('/candidate/all')
      .then(response => {
        this.candidates = response.data;
      })
      .catch(error => {
        console.error('獲取候選項目失敗：', error);
      });
  },
  methods: {
    addCandidate(candidateObj) {
      this.candidates.unshift(candidateObj);
    },
    checkCandidate(id) {
      this.candidates.forEach(candidate => {
        if (candidate.id === id) candidate.selected = !candidate.selected;
      });
    },
    deleteCandidate(id) {
      this.candidates = this.candidates.filter(candidate => candidate.id !== id);
    },
    checkAllCandidate(selected) {
      this.candidates.forEach(candidate => {
        candidate.selected = selected;
      });
    },
    clearAllCandidate() {
      this.candidates = this.candidates.filter(candidate => !candidate.selected);
    },
    toggleMode(mode) {
      this.isAdmin = mode === '管理員模式'; // 切換模式
    },
  },
};
</script>

